export default {
	data() {
		return {
			datalist: [], // 词组列表
			empty:{},
			push:"push",
			change:"change",
		}
	},
	methods: {
		// 获取词组列表
		getdatalist() {
			let that = this;
			that.$request.get("/authorPhrase/search?searchText=" + " ").then(res => {
					console.log(res, "请求的数据")
					that.datalist = res.data;
				})
				.catch(error => {
					console.error('error:', error);
				});
		},
		deletedata(phraseName) {
			let that = this;
			uni.showModal({
			    title: '提示',
			    content: '确定删除此条数据吗',
			    success: function (res) {
			        if (res.confirm) {
						
						that.$request.delete("/authorPhrase/" + phraseName).then(res => {
								that.getdatalist();
								// console.log(res);
							})
							.catch(error => {
								console.error('error:', error);
							});						
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		operation(data,type){
			if(type ==="push"){
				uni.navigateTo({
					 url: './add_data/add_data'
				})
			}else if(type ==="change"){
				let dictionaries=JSON.stringify(data);
				uni.navigateTo({
					 url: './add_data/add_data?dictionariesinfo='+dictionaries
				})
			}
			
		}
	},
	mounted: function() {
		this.getdatalist();
		// this.getTableData();

	},
}
